body{background: #d7faf5}
.App {width: 700px;margin:0 auto;display: flex;    flex-wrap: wrap;padding: 15px;    box-shadow: 0 0 10px rgb(0 0 0 / 10%);background: #fff;border-radius: 4px;}
form{width: 100%}
.student_name{margin-top: 10px;float: left; margin-right: 10px;margin-bottom: 15px;position: relative;}
.student_name span{position: absolute;color:red;display: block;top: 32px;left: 95px;font-size: 10px;text-transform: uppercase;}
.student_name label{margin-right: 5px;}
input{border-radius: 5px;padding: 7px 10px;border:none;border:1px solid #d3d3d3;}
.table{margin:20px 0 20px 0;}
.button_click{cursor: pointer;background: #06b199;padding: 7px 20px;border:1px solid #1b574e;color:#fff;border-radius: 5px;}
.report_card table tr td{padding: 5px}
.table table tr td{padding: 3px}
.table table tr td input{width: 90%}